img {
  max-width: 100%;
  object-fit: contain;
}
a {
  text-decoration: none;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  min-height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content min-content;
  min-height: 100vh;
  margin: 0 auto;
  font-family: "Arial", "Helvetica", sans-serif;
}
.main-header {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}
.main-header--index {
  position: relative;
}
@media (min-width: 1440px) {
  .main-header--index::before {
    content: "";
    position: absolute;
    left: 50%;
    width: 50%;
    height: 694px;
    z-index: -1;
    background-image: linear-gradient(#68b738d9, #68b738d9), url("../img/index-background-cat-desktop.png");
    background-size: cover, 100% 694px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header--index::before {
    background-image: linear-gradient(#68b738d9, #68b738d9), url("../img/index-background-cat-desktop@2x.png");
    background-size: 100% 694px;
  }
}
