.main-navigation {
  position: relative;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__item {
    display: none;
  }
  .main-navigation--opened .main-navigation-wrapper {
    position: absolute;
    top: 0;
    z-index: 1;
  }
  .main-navigation--opened .main-navigation__toggle {
    background-image: url("../img/icons/toggle-opened.svg");
    background-repeat: no-repeat;
  }
  .main-navigation--closed .main-navigation__toggle {
    background-image: url("../img/icons/toggle-closed.svg");
    background-repeat: no-repeat;
  }
  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }
  .main-navigation--nojs .main-navigation-wrapper {
    position: static;
    min-height: 0;
  }
  .main-navigation--nojs .main-navigation__item {
    display: block;
  }
}
